      </b-row>
      <b-row class="mt-2">
        <b-col>
          <b-button size="sm" 
                    v-on:click="toggleAll" 
                    v-if="collapsed">
            <i class="fa fa-angle-double-right"></i> {{ label.expandAll }}
          </b-button>
          <b-button size="sm" 
                    v-on:click="toggleAll" 
                    v-if="!collapsed">
            <i class="fa fa-angle-double-down"></i> {{ label.collapseAll }}
          </b-button>
        </b-col>
        <b-col>
          <b-pagination align="center"
                        size="sm"
                        class="mb-0"
                        v-bind:total-rows="correspDesc.length"
                        v-model="page"
                        v-bind:per-page="10">
          </b-pagination>
        </b-col>
        <b-col>
          <b-button v-on:click="addCorrespDescItem" 
                    size="sm"
                    variant="secondary"
                    class="float-right">
            <i class="fas fa-plus-circle"></i> {{ label.addCdItem }}
          </b-button>
        </b-col>
