</template>

<script>
export default {
  name: 'correspDescData',
  data() {
    return {
      label: this.labels,

      page: 1,
      filter: {
        type: 'name',
        text: '',
        options: [
          { value: 'name', text: 'by Name' },
          { value: 'place', text: 'by Place' },
          { value: 'date', text: 'by Date' },
          { value: 'key', text: 'by Key' },
        ],
      },

      type: ['sender', 'receiver'],

      collapsed: false,

      nextKey: (this.correspDescData.length > 1) ? (this.correspDescData.length) : 1,
      correspDesc: this.correspDescData,

      persons: this.pArrays.persons,
      places: this.pArrays.places,
    };
  },
  props: {
    labels: {
      type: Object,
      default: () => ({}),
    },
    pArrays: {
      type: Object,
      twoWays: true,
      default: () => ({
        persons: [],
        places: [],
      }),
    },
    correspDescData: {
      type: Array,
      twoWays: true,
      default() {
        return {
          id: 0,
          visible: true,

          header: '',

          ref: '',
          refHidden: true,

          key: '',

          bibl: 0,

          sender: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: 0,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
          receiver: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: 0,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
        };
      },
    },
  },
  methods: {
    // Validate Entries
    state(item, id, key) {
      const correspDescKey = this.getRealKey(id);
      if (item === 'urlToXml') {
        if (this.correspDesc[correspDescKey].ref !== '') {
          return this.$parent.check('url', this.correspDesc[correspDescKey].ref);
        }
      }
      if (item === 'senderAuthority' || item === 'receiverAuthority') {
        if (this.correspDesc[correspDescKey][item.replace('Authority', '')].persName[key].ref !== '') {
          return this.$parent.check('person', this.correspDesc[correspDescKey][item.replace('Authority', '')].persName[key].ref);
        }
      }
      if (item === 'senderPlaceAuthority' || item === 'receiverPlaceAuthority') {
        if (this.correspDesc[correspDescKey][item.replace('PlaceAuthority', '')].placeName.ref !== '') {
          return this.$parent.check('place', this.correspDesc[correspDescKey][item.replace('PlaceAuthority', '')].placeName.ref);
        }
      }
      if (item.includes('when')
          || item.includes('notBefore')
          || item.includes('notAfter')
          || item.includes('spanFrom')
          || item.includes('spanTo')) {
        if (this.correspDesc[correspDescKey][(item.includes('Sender') ? 'sender' : 'receiver')][item.replace(/Receiver|Sender/g, '')] !== '') {
          return this.$parent.check('date', this.correspDesc[correspDescKey][(item.includes('Sender') ? 'sender' : 'receiver')][item.replace(/Receiver|Sender/g, '')]);
        }
      }
      return null;
    },

    // Change header of items according to content
    setHeader(id) {
      const correspDescKey = this.getRealKey(id);
      this.correspDesc[correspDescKey].header = (this.correspDesc[correspDescKey].key)
      ? `<span class="badge badge-info mr-1">${this.correspDesc[correspDescKey].key}</span>` : '';
      this.correspDesc[correspDescKey].header += (this.correspDesc[correspDescKey].sender.persName[0].text)
      ? `<span class="badge badge-secondary mr-1">${this.label.headBy}</span><small class="mr-2 ml-1">${this.correspDesc[correspDescKey].sender.persName[0].text}</small>` : '';
      this.correspDesc[correspDescKey].header += (this.correspDesc[correspDescKey].receiver.persName[0].text)
      ? `<span class="badge badge-secondary mr-1">${this.label.headTo}</span><small class="mr-2 ml-1">${this.correspDesc[correspDescKey].receiver.persName[0].text}</small>` : '';
    },

    // Get Keys from ID
    getRealKey(id) {
      for (let i = 0; i < this.correspDesc.length; i += 1) {
        if (this.correspDesc[i].id === id) return i;
      }
      return 0;
    },

    // Get Data from Geonames
    getGeodata(target, id) {
      const correspDescKey = this.getRealKey(id);
      const xhr = new XMLHttpRequest();
      let json = '';
      xhr.open('GET', `http://api.geonames.org/searchJSON?q=${this.correspDesc[correspDescKey][target].placeName.text}&featureClass=P&maxRows=10&username=cosea`);
      xhr.onload = () => {
        if (xhr.status === 200) {
          json = JSON.parse(xhr.responseText);
          this.correspDesc[correspDescKey][target].placeName.geo.suggestions = json.geonames;
          this.correspDesc[correspDescKey][target].placeName.geo.all = json.totalResultsCount;
          if (json.totalResultsCount > 0) {
            this.correspDesc[correspDescKey][target].placeName.geo.open = true;
          }
          this.$nextTick(() => {
            this.$refs[`${target}PlaceID${id}`][0].$el.focus();
          });
        } else {
          console.info(xhr.status);
        }
      };
      xhr.send();
    },

    // Select-Routine for Geoname-Suggestions
    selectGeoname(target, id, geoData) {
      const correspDescKey = this.getRealKey(id);
      for (let i = 0; i < this.places.length; i += 1) {
        if (this.places[i].text === this.correspDesc[correspDescKey][target].placeName.text) {
          this.places[i].text = geoData.name;
          this.places[i].ref = `http://www.geonames.org/${geoData.geonameId}`;
        }
      }
      this.correspDesc[correspDescKey][target].placeName.text = geoData.name;
      this.correspDesc[correspDescKey][target].placeName.ref = `http://www.geonames.org/${geoData.geonameId}`;
      this.correspDesc[correspDescKey][target].placeName.geo.suggestions = [];
      this.correspDesc[correspDescKey][target].placeName.geo.open = false;
      this.correspDesc[correspDescKey][target].placeName.geo.highlighted = 0;

      this.correspDesc[correspDescKey][target].placeName.selected = true;
    },

    // Filter Names and Places for Suggestions
    filterNames(target, id, persKey, place = false) {
      const correspDescKey = this.getRealKey(id);
      const results = [];
      if (!place) {
        const persName = this.correspDesc[correspDescKey][target].persName[persKey].text;
        const regEx = new RegExp(persName, 'i');
        for (let i = 0; i < this.persons.length; i += 1) {
          if (this.persons[i].text.match(regEx)) {
            results.push(this.persons[i].text);
          }
        }
      } else if (this.correspDesc[correspDescKey][target].placeName.text) {
        const placeName = this.correspDesc[correspDescKey][target].placeName.text;
        const regEx = new RegExp(placeName, 'i');
        for (let i = 0; i < this.places.length; i += 1) {
          if (this.places[i].text !== undefined && this.places[i].text.match(regEx)) {
            results.push(this.places[i].text);
          }
        }
      }
      return results;
    },

    // Change pers or org according to input
    addOrg(target, id, persKey) {
      const correspDescKey = this.getRealKey(id);
      if (this.correspDesc[correspDescKey][target].persName[persKey].text) {
        for (let i = 0; i < this.persons.length; i += 1) {
          if (this.persons[i].text === this.correspDesc[correspDescKey][target].persName[persKey].text) {
            this.persons[i].org = this.correspDesc[correspDescKey][target].persName[persKey].organisation;
          }
        }
      }
      // ** Set other entries accordingly **
      // Optional: Comment out to increase performance
      if (this.correspDesc[correspDescKey][target].persName[persKey].text) {
        for (let j = 0; j < this.correspDesc.length; j += 1) {
          this.type.forEach((e) => {
            for (let i = 0; i < this.correspDesc[j][e].persName.length; i += 1) {
              if (this.correspDesc[j][e].persName[i].text === this.correspDesc[correspDescKey][target].persName[persKey].text) {
                this.correspDesc[j][e].persName[i].organisation = this.correspDesc[correspDescKey][target].persName[persKey].organisation;
              }
            }
          });
        }
      }
    },

    // Add Norm-ID for Person
    addPersNorm(target, id, persKey) {
      const correspDescKey = this.getRealKey(id);
      if (this.correspDesc[correspDescKey][target].persName[persKey].text !== '' && this.correspDesc[correspDescKey][target].persName[persKey].ref !== '') {
        for (let i = 0; i < this.persons.length; i += 1) {
          if (this.persons[i].text === this.correspDesc[correspDescKey][target].persName[persKey].text) {
            if (this.persons[i].ref !== this.correspDesc[correspDescKey][target].persName[persKey].ref) {
              this.persons[i].ref = this.correspDesc[correspDescKey][target].persName[persKey].ref;
              break;
            }
          }
        }
      }
      // ** Set other entries accordingly **
      // Optional: Comment out to increase performance
      if (this.correspDesc[correspDescKey][target].persName[persKey].text) {
        for (let j = 0; j < this.correspDesc.length; j += 1) {
          this.type.forEach((e) => {
            for (let i = 0; i < this.correspDesc[j][e].persName.length; i += 1) {
              if (this.correspDesc[j][e].persName[i].text === this.correspDesc[correspDescKey][target].persName[persKey].text) {
                this.correspDesc[j][e].persName[i].ref = this.correspDesc[correspDescKey][target].persName[persKey].ref;
              }
            }
          });
        }
      }
    },

    // Add Person to Pool
    addPersonToPool(target, id, persKey) {
      const correspDescKey = this.getRealKey(id);
      if (this.persons.length === 0) {
        this.persons.push({
          text: this.correspDesc[correspDescKey][target].persName[persKey].text,
          ref: this.correspDesc[correspDescKey][target].persName[persKey].ref,
          org: this.correspDesc[correspDescKey][target].persName[persKey].organisation,
        });
      } else {
        let match = false;
        for (let i = 0; i < this.persons.length; i += 1) {
          match = (this.persons[i].text === this.correspDesc[correspDescKey][target].persName[persKey].text);
          if (match) break;
        }
        if (!match) {
          this.persons.push({
            text: this.correspDesc[correspDescKey][target].persName[persKey].text,
            ref: this.correspDesc[correspDescKey][target].persName[persKey].ref,
            org: this.correspDesc[correspDescKey][target].persName[persKey].organisation,
          });
        }
      }
      this.correspDesc[correspDescKey][target].persName[persKey].open = false;
    },

    // Add Norm-ID for Place
    addPlaceNorm(target, id) {
      const correspDescKey = this.getRealKey(id);
      if (this.correspDesc[correspDescKey][target].placeName.text !== '' && this.correspDesc[correspDescKey][target].placeName.ref !== '') {
        for (let i = 0; i < this.places.length; i += 1) {
          if (this.places[i].text === this.correspDesc[correspDescKey][target].placeName.text) {
            if (this.places[i].ref !== this.correspDesc[correspDescKey][target].placeName.ref) {
              this.places[i].ref = this.correspDesc[correspDescKey][target].placeName.ref;
              break;
            }
          }
        }
      }
      // ** Set other entries accordingly **
      // Optional: Comment out to increase performance
      if (this.correspDesc[correspDescKey][target].placeName.text) {
        for (let j = 0; j < this.correspDesc.length; j += 1) {
          this.type.forEach((e) => {
            for (let i = 0; i < this.correspDesc[j][e].placeName.length; i += 1) {
              if (this.correspDesc[j][e].placeName.text === this.correspDesc[correspDescKey][target].placeName.text) {
                this.correspDesc[j][e].placeName.ref = this.correspDesc[correspDescKey][target].placeName.ref;
              }
            }
          });
        }
      }
    },

    // Add Place to Pool
    addPlaceToPool(target, id) {
      const correspDescKey = this.getRealKey(id);
      if (this.places.length === 0) {
        this.places.push({
          text: this.correspDesc[correspDescKey][target].placeName.text,
          ref: this.correspDesc[correspDescKey][target].placeName.ref,
        });
      } else {
        let match = false;
        for (let i = 0; i < this.places.length; i += 1) {
          match = (this.places[i].text === this.correspDesc[correspDescKey][target].placeName.text);
          if (match) break;
        }
        if (!match) {
          this.places.push({
            text: this.correspDesc[correspDescKey][target].placeName.text,
            ref: this.correspDesc[correspDescKey][target].placeName.ref,
          });
        }
      }
      this.correspDesc[correspDescKey][target].placeName.open = false;
    },

    // Event to trigger the Suggestions-List
    onInput(target, id, persKey, place = false) {
      const correspDescKey = this.getRealKey(id);
      if (!place) {
        if (this.correspDesc[correspDescKey][target].persName[persKey].selected) {
          this.correspDesc[correspDescKey][target].persName[persKey].selected = false;
        } else if (this.correspDesc[correspDescKey][target].persName[persKey].text.length > 2) {
          if (this.persons.length > 0) {
            this.correspDesc[correspDescKey][target].persName[persKey].open = !!(this.correspDesc[correspDescKey][target].persName[persKey].text);
            this.correspDesc[correspDescKey][target].persName[persKey].highlighted = (this.correspDesc[correspDescKey][target].persName[persKey].highlighted === 0) ? 0 : this.correspDesc[correspDescKey][target].persName[persKey].highlighted;
          }
        }
      }
      if (place) {
        if (this.correspDesc[correspDescKey][target].placeName.text !== null) {
          if (this.correspDesc[correspDescKey][target].placeName.selected) {
            this.correspDesc[correspDescKey][target].placeName.selected = false;
          } else if (this.places.length > 0) {
            this.correspDesc[correspDescKey][target].placeName.open = !!(this.correspDesc[correspDescKey][target].placeName.text);
            this.correspDesc[correspDescKey][target].placeName.highlighted = (this.correspDesc[correspDescKey][target].placeName.highlighted === 0) ? 0 : this.correspDesc[correspDescKey][target].placeName.highlighted;
          }
        }
      }
    },

    // Select from Suggestions-List
    select(target, id, persKey, place = false) {
      const correspDescKey = this.getRealKey(id);
      if (!place) {
        const selected = this.filterNames(target, correspDescKey, persKey)[this.correspDesc[correspDescKey][target].persName[persKey].highlighted];
        for (let i = 0; i < this.persons.length; i += 1) {
          if (selected === this.persons[i].text) {
            this.correspDesc[correspDescKey][target].persName[persKey].text = this.persons[i].text;
            this.correspDesc[correspDescKey][target].persName[persKey].ref = this.persons[i].ref;
            this.correspDesc[correspDescKey][target].persName[persKey].organisation = this.persons[i].org;
            this.correspDesc[correspDescKey][target].persName[persKey].open = false;
            this.correspDesc[correspDescKey][target].persName[persKey].selected = true;
            break;
          }
        }
      } else {
        const selected = this.filterNames(target, correspDescKey, 0, true)[this.correspDesc[correspDescKey][target].placeName.highlighted];
        for (let i = 0; i < this.places.length; i += 1) {
          if (this.places[i].text !== undefined && selected === this.places[i].text) {
            this.correspDesc[correspDescKey][target].placeName.text = this.places[i].text;
            this.correspDesc[correspDescKey][target].placeName.ref = this.places[i].ref;
            this.correspDesc[correspDescKey][target].placeName.open = false;
            this.correspDesc[correspDescKey][target].placeName.selected = true;
            break;
          }
        }
      }
    },

    // Move Up by Key-Input in List of Suggestions
    moveUp(target, id, persKey, place = false, geo = false) {
      const correspDescKey = this.getRealKey(id);
      if (!place) {
        if (this.correspDesc[correspDescKey][target].persName[persKey].open) {
          this.correspDesc[correspDescKey][target].persName[persKey].highlighted = ((this.correspDesc[correspDescKey][target].persName[persKey].highlighted - 1) < 0)
          ? this.filterNames(target, correspDescKey, persKey).length - 1
          : (this.correspDesc[correspDescKey][target].persName[persKey].highlighted - 1) % this.filterNames(target, correspDescKey, persKey).length;
        }
      } else if (this.correspDesc[correspDescKey][target].placeName.open) {
        this.correspDesc[correspDescKey][target].placeName.highlighted = ((this.correspDesc[correspDescKey][target].placeName.highlighted - 1) < 0)
        ? this.filterNames(target, correspDescKey, 0, true).length - 1
        : (this.correspDesc[correspDescKey][target].placeName.highlighted - 1) % this.filterNames(target, correspDescKey, 0, true).length;
      } else if (geo) {
        if (this.correspDesc[correspDescKey][target].placeName.geo.open) {
          this.correspDesc[correspDescKey][target].placeName.geo.highlighted = ((this.correspDesc[correspDescKey][target].placeName.geo.highlighted - 1) < 0)
          ? this.correspDesc[correspDescKey][target].placeName.geo.all - 1
          : (this.correspDesc[correspDescKey][target].placeName.geo.highlighted - 1) % this.correspDesc[correspDescKey][target].placeName.geo.all;
        }
      }
    },

    // Move Down by Key-Input in List of Suggestions
    moveDown(target, id, persKey, place = false, geo = false) {
      const correspDescKey = this.getRealKey(id);
      if (!place) {
        if (this.correspDesc[correspDescKey][target].persName[persKey].open) {
          this.correspDesc[correspDescKey][target].persName[persKey].highlighted = (this.correspDesc[correspDescKey][target].persName[persKey].highlighted + 1) % this.filterNames(target, correspDescKey, persKey).length;
        }
      } else if (this.correspDesc[correspDescKey][target].placeName.open) {
        this.correspDesc[correspDescKey][target].placeName.highlighted = (this.correspDesc[correspDescKey][target].placeName.highlighted + 1) % this.filterNames(target, correspDescKey, 0, true).length;
      } else if (geo) {
        if (this.correspDesc[correspDescKey][target].placeName.geo.open) {
          this.correspDesc[correspDescKey][target].placeName.geo.highlighted = (this.correspDesc[correspDescKey][target].placeName.geo.highlighted + 1) % this.correspDesc[correspDescKey][target].placeName.geo.all;
        }
      }
    },

    // Add new Person
    addPerson(target, key) {
      this.correspDesc[key][target].persName.push({
        text: '',
        ref: '',

        conjecture: false,
        uncertain: false,

        organisation: false,

        // Autocomplete
        open: false,
        highlighted: 0,
        selected: false,
      });
    },

    // Delete Person
    rmPerson(target, id, persKey) {
      const correspDescKey = this.getRealKey(id);
      this.correspDesc[correspDescKey][target].persName.splice(persKey, 1);
      for (let i = 0; i < this.correspDesc[correspDescKey][target].persName.length; i += 1) {
        this.correspDesc[correspDescKey][target].persName[i].selected = true;
      }
    },

    // Add new correspDesc-Item
    addCorrespDescItem() {
      this.filter.text = '';
      for (let i = 0; i < this.filteredCorrespDesc.length; i += 1) {
        this.filteredCorrespDesc[i].visible = false;
      }
      this.correspDesc.push({
        id: this.nextKey,
        visible: true,

        header: '',

        ref: '',
        refHidden: true,

        key: '',

        bibl: 0,

        sender: {
          persName: [
            {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              organisation: false,

              // Autocomplete
              open: false,
              highlighted: false,
              selected: false,
            },
          ],
          placeName: {
            text: '',
            ref: '',

            conjecture: false,
            uncertain: false,

            // Autocomplete
            open: false,
            highlighted: false,
            selected: false,

            // Geonames Suggestions
            geo: {
              open: false,
              highlighted: false,
              suggestions: [],
              all: 0,
            },
          },

          date: '',
          dateCert: {
            conjecture: false,
            uncertain: false,
          },
          when: '',
          notBefore: '',
          notAfter: '',
          spanFrom: '',
          spanTo: '',
          dateAsText: '',
          dateAsTextHidden: true,
        },
        receiver: {
          persName: [
            {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              organisation: false,

              // Autocomplete
              open: false,
              highlighted: false,
              selected: false,
            },
          ],
          placeName: {
            text: '',
            ref: '',

            conjecture: false,
            uncertain: false,

            // Autocomplete
            open: false,
            highlighted: false,
            selected: false,

            // Geonames Suggestions
            geo: {
              open: false,
              highlighted: false,
              suggestions: [],
              all: 0,
            },
          },

          date: '',
          dateCert: {
            conjecture: false,
            uncertain: false,
          },
          when: '',
          notBefore: '',
          notAfter: '',
          spanFrom: '',
          spanTo: '',
          dateAsText: '',
          dateAsTextHidden: true,
        },
      });
      this.nextKey += 1;
      this.page = Math.ceil(this.correspDesc.length / 10);
    },

    // Delete correspDesc-Item
    rmCorrespDescItem(key) {
      for (let i = 0; i < this.correspDesc.length; i += 1) {
        if (key === this.correspDesc[i].id) {
          this.correspDesc.splice(i, 1);
        }
      }
    },

    // Collapse / Expand all Items
    toggleAll() {
      for (let i = 0; i < this.filteredCorrespDesc.length; i += 1) {
        this.filteredCorrespDesc[i].visible = this.collapsed;
      }
      this.collapsed = !this.collapsed;
    },
  },
  computed: {
    // Get filtered Results & Paginate them
    filteredCorrespDesc() {
      if (this.filter.text === '') {
        return this.correspDesc.slice((this.page === 1) ? 0 : ((this.page - 1) * 10), ((this.page === 1) ? 0 : ((this.page - 1) * 10)) + 10);
      }
      const results = [];
      if (this.filter.type === 'name') {
        for (let i = 0; i < this.correspDesc.length; i += 1) {
          let match = false;
          for (let j = 0; j < this.correspDesc[i].sender.persName.length; j += 1) {
            if (this.correspDesc[i].sender.persName[j].text.includes(this.filter.text)) {
              results.push(this.correspDesc[i]);
              match = true;
            }
          }
          for (let j = 0; j < this.correspDesc[i].receiver.persName.length; j += 1) {
            if (this.correspDesc[i].receiver.persName[j].text.includes(this.filter.text)) {
              if (!match) results.push(this.correspDesc[i]);
            }
          }
        }
      }
      if (this.filter.type === 'place') {
        for (let i = 0; i < this.correspDesc.length; i += 1) {
          let match = false;
          if (this.correspDesc[i].sender.placeName.text.includes(this.filter.text)) {
            results.push(this.correspDesc[i]);
            match = true;
          }
          if (this.correspDesc[i].receiver.placeName.text.includes(this.filter.text)) {
            if (!match) results.push(this.correspDesc[i]);
          }
        }
      }
      if (this.filter.type === 'key') {
        for (let i = 0; i < this.correspDesc.length; i += 1) {
          if (this.correspDesc[i].key === this.filter.text) {
            results.push(this.correspDesc[i]);
          }
        }
      }
      if (this.filter.type === 'date') {
        for (let i = 0; i < this.correspDesc.length; i += 1) {
          let match = false;
          if (this.correspDesc[i].sender.when && this.correspDesc[i].sender.when.includes(this.filter.text)) {
            results.push(this.correspDesc[i]);
            match = true;
          }
          if ((this.correspDesc[i].sender.notBefore && this.correspDesc[i].sender.notBefore.includes(this.filter.text))
            || (this.correspDesc[i].sender.notAfter && this.correspDesc[i].sender.notAfter.includes(this.filter.text))
            || (this.correspDesc[i].sender.spanFrom && this.correspDesc[i].sender.spanFrom.includes(this.filter.text))
            || (this.correspDesc[i].sender.spanTo && this.correspDesc[i].sender.spanTo.includes(this.filter.text))
            || (this.correspDesc[i].sender.dateAsText && this.correspDesc[i].sender.dateAsText.includes(this.filter.text))) {
            if (!match) {
              results.push(this.correspDesc[i]);
              match = true;
            }
          }
          if ((this.correspDesc[i].receiver.when && this.correspDesc[i].receiver.when.includes(this.filter.text))
            || (this.correspDesc[i].receiver.notBefore && this.correspDesc[i].receiver.notBefore.includes(this.filter.text))
            || (this.correspDesc[i].receiver.notAfter && this.correspDesc[i].receiver.notAfter.includes(this.filter.text))
            || (this.correspDesc[i].receiver.spanFrom && this.correspDesc[i].receiver.spanFrom.includes(this.filter.text))
            || (this.correspDesc[i].receiver.spanTo && this.correspDesc[i].receiver.spanTo.includes(this.filter.text))
            || (this.correspDesc[i].receiver.dateAsText && this.correspDesc[i].receiver.dateAsText.includes(this.filter.text))) {
            if (!match) {
              results.push(this.correspDesc[i]);
              match = true;
            }
          }
        }
      }
      return results.slice((this.page === 1) ? 0 : ((this.page - 1) * 10), ((this.page === 1) ? 0 : ((this.page - 1) * 10)) + 10);
    },
  },
};
</script>


