<template>
  <div class="biblData">
    <b-container>
      <b-row>
        <b-col>
          <h3>{{ label.bibInfo }}</h3>
          {{ label.step2Desc }}
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col>
          <b-card v-for="item in bibl"
                  v-bind:key="item.id"
                  class="mb-2">
            <b-row align-h="end" 
                   v-if="bibl.length > 1">
              <b-col cols="1">
                <b-button size="sm"
                          class="w-100 mb-2"
                          v-on:click="rmBiblItem(item.id)">
                  <i class="fa fa-trash-alt"></i>
                </b-button>
              </b-col>
            </b-row>
            <b-form-group horizontal
                          v-bind:label-cols="2"
                          label-size="sm"
                          v-bind:label="label.bibType"
                          label-for="biblType">
              <b-form-select id="biblType"
                             v-model="item.type"
                             v-bind:state="state(item,'type')">
                <option v-bind:value="null">{{ label.selectBibType }}</option>
                <option value="online">{{ label.bibTypeOnline }}</option>
                <option value="print">{{ label.bibTypePrint }}</option>
              </b-form-select>
            </b-form-group>
            <b-form-group horizontal
                          v-bind:label-cols="2"
                          label-size="sm"
                          v-bind:label="label.bibTitle"
                          label-for="biblText">
              <b-form-textarea id="biblText"
                               v-model="item.text"
                               v-bind:rows="3"
                               v-bind:max-rows="3"
                               v-bind:state="state(item,'text')"></b-form-textarea>
            </b-form-group>
            <b-form-group horizontal
                          v-if="item.type==='online'"
                          v-bind:label-cols="2"
                          label-size="sm"
                          v-bind:label="label.bibUrl"
                          label-for="biblUrl">
              <b-form-input id="biblUrl"
                            size="sm"
                            v-model="item.url"
                            v-bind:state="state(item,'url')"></b-form-input>
            </b-form-group>
          </b-card>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-button v-on:click="addBiblItem" 
                    size="sm" 
                    variant="secondary">
            <i class="fas fa-plus-circle"></i> {{ label.addBib }}
          </b-button>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'biblData',
  props: {
    labels: {
      type: Object,
      default: () => ({}),
    },
    biblData: {
      type: Array,
      twoWays: true,
      default() {
        return {
          id: 0,
          uuid: '',
          type: null,
          url: '',
          text: '',
          selected: '',
        };
      },
    },
  },
  data() {
    return {
      label: this.labels,

      nextKey: 1,
      bibl: this.biblData,
    };
  },
  methods: {
    // Check values
    state(item, field) {
      let state = false;
      if (field === 'url') {
        if (item.url !== '') {
          state = this.$parent.check('url', item.url);
        } else {
          state = null;
        }
      } else if (item[field] !== '' && item[field] !== null) {
        state = true;
      }
      return state;
    },

    // Add new Bibliographical Item
    addBiblItem() {
      this.bibl.push({
        id: this.nextKey,
        uuid: this.uuid(),
        type: null,
        url: '',
        text: '',
        selected: '',
      });
      this.nextKey += 1;
    },

    // Remove Bibliographical Item
    rmBiblItem(key) {
      for (let i = 0; i < this.bibl.length; i += 1) {
        if (key === this.bibl[i].id) {
          this.bibl.splice(i, 1);
        }
      }
    },

    // Generate and return uuid v4
    uuid() {
      const result = ([1e7] + -1e3 + -4e3 + -8e3 + -1e11).replace(/[018]/g, c =>
        (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)); // eslint-disable-line no-bitwise, no-mixed-operators
      return result;
    },
  },
  mounted() {
    this.bibl[0].uuid = (this.bibl[0].uuid === '') ? this.uuid() : this.bibl[0].uuid;
  },
};
</script>



