<template>
  <div class="metaData">
    <b-container>
      <b-row>
        <b-col>
          <h3>{{ label.metaData }}</h3>
          {{ label.step1Desc }}
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.title+'*'"
                        label-for="title">
            <b-form-input id="title"
                          size="sm"
                          v-bind:state="state('title')"
                          v-model="meta.title"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.editor+'*'"
                        label-for="editor">
            <b-form-input id="editor"
                          size="sm"
                          v-bind:state="state('editor','name')"
                          v-model="meta.editor.name"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.editorEmail+'*'"
                        label-for="editorEmail">
            <b-form-input id="editorEmail"
                          size="sm"
                          v-bind:state="state('editor','email')"
                          type="email"
                          v-model="meta.editor.email"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.publisher+'*'"
                        label-for="publisher">
            <b-form-input id="publisher"
                          size="sm"
                          v-bind:state="state('publisher','name')"
                          v-model="meta.publisher.name"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.publisherWebsite"
                        label-for="publisherWebsite">
            <b-form-input id="publisherWebsite"
                          size="sm"
                          type="url"
                          v-model="meta.publisher.url"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.urlToXml"
                        label-for="urlToXml">
            <b-form-input id="urlToXml"
                          size="sm"
                          type="url"
                          v-model="meta.url"></b-form-input>
          </b-form-group>
          <b-form-group horizontal
                        v-bind:label-cols="2"
                        label-size="sm"
                        v-bind:label="label.licence+'*'"
                        label-for="licence">
            <b-form-select v-bind:options="licenceOptions"
                           size="sm"
                           v-bind:state="state('licence')"
                           v-model="meta.licence" />
          </b-form-group>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'metaData',
  props: {
    labels: {
      type: Object,
      default: () => ({}),
    },
    metaData: {
      type: Object,
      twoWays: true,
      default() {
        return {
          title: '',
          editor: {
            name: '',
            email: '',
          },
          publisher: {
            name: '',
            url: '',
          },
          url: '',
          licence: '',
        };
      },
    },
  },
  data() {
    return {
      label: this.labels,

      licenceOptions: [
        { value: 'CC0', text: 'CC0' },
        { value: 'CC-BY 4.0', text: 'CC-BY 4.0' },
      ],

      meta: this.metaData,
    };
  },
  methods: {
    state(item, field = '') {
      let state = false;
      if (field === '') {
        state = (this.meta[item] !== '');
      } else if (field === 'email') {
        state = this.$parent.check('email', this.meta[item].email);
      } else {
        state = (this.meta[item][field] !== '');
      }
      return state;
    },
  },
  computed: {},
};
</script>


