<template>
  <div class="save">
    <b-container>
      <b-row>
        <b-col>
          {{ label.step4Desc }}
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col>
          <b-list-group>
            <b-list-group-item v-bind:variant="(validate('meta')) ? 'success' : 'danger'">
              {{ labels.step1 }}:
              <i class="fas fa-check ml-3" 
                 v-if="validate('meta')"></i>
              <i class="fas fa-times ml-3" 
                 v-if="!validate('meta')"></i>
            </b-list-group-item>
            <b-list-group-item v-bind:variant="(validate('bibl')) ? 'success' : 'danger'">
              {{ labels.step2 }}:
              <i class="fas fa-check ml-3" 
                 v-if="validate('bibl')"></i>
              <i class="fas fa-times ml-3" 
                 v-if="!validate('bibl')"></i>
            </b-list-group-item>
            <b-list-group-item v-bind:variant="(validate('correspDesc')) ? 'success' : 'danger'">
              {{ labels.step3 }}:
              <i class="fas fa-check ml-3" 
                 v-if="validate('correspDesc')"></i>
              <i class="fas fa-times ml-3" 
                 v-if="!validate('correspDesc')"></i>
              <ul v-if="!validate('correspDesc',true) && getInvalidCorrespDesc.length > 1">
                <li v-for="(item, errKey) in getInvalidCorrespDesc" 
                    v-bind:key="errKey"
                    class="d-block">
                {{ item.id }}: [Key: {{ item.key }}] {{ label.headBy }} {{ item.from }} {{ label.headTo }} {{ item.to }}
                </li>
              </ul>
            </b-list-group-item>
          </b-list-group>
        </b-col>
      </b-row>
      <b-row v-if="validate('meta') && validate('bibl') && validate('correspDesc')">
        <b-col class="mt-3">
          <b-button v-bind:href='downloadLink' 
                    class="text-white btn-block" 
                    size="lg" 
                    v-bind:download='downloadFileName.json'>{{ label.download }}</b-button>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'save',
  props: {
    labels: {
      type: Object,
      default: () => ({}),
    },
    metaData: {
      type: Object,
      default() {
        return {
          title: '',
          editor: {
            name: '',
            email: '',
          },
          publisher: {
            name: '',
            url: '',
          },
          url: '',
          licence: '',
        };
      },
    },
    biblData: {
      type: Array,
      default() {
        return {
          id: 0,
          uuid: '',
          type: null,
          url: '',
          text: '',
          selected: '',
        };
      },
    },
    correspDescData: {
      type: Array,
      default() {
        return {
          id: 0,

          header: '',

          ref: '',
          refHidden: true,

          key: '',

          bibl: 0,

          sender: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: 0,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
          receiver: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: 0,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
        };
      },
    },
  },
  data() {
    return {
      label: this.labels,

      meta: this.metaData,
      bibl: this.biblData,
      correspDesc: this.correspDescData,

      downloadLink: '',
      downloadFileName: {
        json: '',
        xml: '',
      },

      correspDescFails: [],
    };
  },
  methods: {
    // Validate Values
    validate(target, correspDescFails = false) {
      let status = {
        meta: false,
        bibl: false,
        correspDesc: false,
      };
      switch (target) {
        case 'meta': {
          if (this.meta.title !== ''
            && this.meta.editor.name !== ''
            && this.meta.editor.email !== ''
            && this.$parent.check('email', this.meta.editor.email)
            && this.meta.publisher.name !== ''
            && this.meta.licence !== '') {
            status.meta = true;
          }
          break;
        }
        case 'bibl': {
          if (this.bibl.length > 0) {
            for (let i = 0; i < this.bibl.length; i += 1) {
              if (this.bibl[i].type !== null
                && this.bibl[i].text !== '') {
                if (this.bibl[i].url !== '' && !this.$parent.check('url', this.bibl[i].url) && this.bibl[i].type === 'online') {
                  status.bibl = false;
                } else {
                  status.bibl = true;
                }
              }
            }
          }
          break;
        }
        case 'correspDesc': {
          let state = true;
          for (let i = 0; i < this.correspDesc.length; i += 1) {
            if (this.correspDesc[i].ref !== ''
              && !this.$parent.check('url', this.correspDesc[i].ref)) {
              state = false;
            }
            ['sender', 'receiver'].forEach((t) => { // eslint-disable-line no-loop-func
              for (let j = 0; j < this.correspDesc[i][t].persName.length; j += 1) {
                if (this.correspDesc[i][t].persName[j].ref !== ''
                  && !this.$parent.check('person', this.correspDesc[i][t].persName[j].ref)) {
                  state = false;
                }
              }
              if (this.correspDesc[i][t].placeName.ref !== ''
                && !this.$parent.check('place', this.correspDesc[i][t].placeName.ref)) {
                state = false;
              }
              if (this.correspDesc[i][t].date === 'when'
                && this.correspDesc[i][t].when !== ''
                && !this.$parent.check('date', this.correspDesc[i][t].when)) {
                state = false;
              }
              if (this.correspDesc[i][t].date === 'nba'
                && ((this.correspDesc[i][t].notBefore !== ''
                && !this.$parent.check('date', this.correspDesc[i][t].notBefore))
                || (this.correspDesc[i][t].notAfter !== ''
                && !this.$parent.check('date', this.correspDesc[i][t].notAfter)))) {
                state = false;
              }
              if (this.correspDesc[i][t].date === 'span'
                && (this.correspDesc[i][t].spanFrom !== ''
                || this.correspDesc[i][t].spanTo !== '')
                && (!this.$parent.check('date', this.correspDesc[i][t].spanFrom)
                || !this.$parent.check('date', this.correspDesc[i][t].spanTo))) {
                state = false;
              }
            });
            if (!state && correspDescFails) {
              let match = false;
              for (let k = 0; k < this.correspDescFails.length; k += 1) {
                if (this.correspDescFails[k] === i) {
                  match = true;
                  break;
                }
              }
              if (!match) this.correspDescFails.push(i);
            }
          }
          status.correspDesc = state;
          break;
        }
        default: {
          status = {
            meta: false,
            bibl: false,
            correspDesc: false,
          };
        }
      }
      return status[target];
    },

    // Prepare Data for Saving
    prepare() {
      let json = {
        teiHeader: {
          fileDesc: {
            titleStmt: {
              title: null,
              editor: {
                '#text': null,
                email: null,
              },
            },
            publicationStmt: {
              publisher: {
                ref: {
                  target: null,
                  '#text': null,
                },
              },
              idno: {
                type: null,
                '#text': null,
              },
              date: {
                when: null,
              },
              availability: {
                licence: {
                  target: null,
                  '#text': null,
                },
              },
            },
            sourceDesc: {
              bibl: [],
            },
          },
          profileDesc: {
            correspDesc: [],
          },
        },
      };
      if (this.validate('meta')
          && this.validate('bibl')
          && this.validate('correspDesc')) {
        // Generating time
        const currentDate = new Date();
        const timezone = currentDate.getTimezoneOffset() / 60;

        let timezoneString = '';
        if (timezone > -10 && timezone < 10) {
          timezoneString = String(timezone);
          timezoneString = (timezoneString.includes('-')) ? timezoneString.replace('-', '-0') : timezoneString = `0${timezoneString}`;
        } else {
          timezoneString = String(timezone);
        }

        let now = `${currentDate.getFullYear()}-`;
        now += `${(`0${(currentDate.getMonth() + 1)}`).slice(-2)}-`;
        now += `${(`0${(currentDate.getDate() + 1)}`).slice(-2)}T`;
        now += `${(`0${(currentDate.getHours() + 1)}`).slice(-2)}:`;
        now += `${(`0${(currentDate.getMinutes() + 1)}`).slice(-2)}:`;
        now += `${(`0${(currentDate.getSeconds() + 1)}`).slice(-2)}.`;
        now += currentDate.getMilliseconds();
        now += `${timezoneString}:00`;

        // Prepare MetaData
        json.teiHeader.fileDesc.titleStmt = {
          title: this.meta.title,
          editor: {
            '#text': this.meta.editor.name,
            email: this.meta.editor.email,
          },
        };
        json.teiHeader.fileDesc.publicationStmt = {
          publisher: {
            ref: {
              '#text': this.meta.publisher.name,
              target: this.meta.publisher.url,
            },
          },
          idno: {
            type: this.meta.url ? 'url' : null,
            '#text': this.meta.url,
          },
          availability: {
            licence: {
              '#text': (this.meta.licence === 'CC0' || this.meta.licence === 'CC-BY 4.0') ? this.meta.licence : null,
              target: `https://creativecommons.org/licenses/by/${this.meta.licence.match(/[0-9]\.[0-9]/g)}/`,
            },
          },
          date: {
            when: now,
          },
        };

        // Prepare BiblData
        for (let i = 0; i < this.bibl.length; i += 1) {
          json.teiHeader.fileDesc.sourceDesc.bibl.push({
            'xml:id': this.bibl[i].uuid, // To be changed
            type: this.bibl[i].type,
            '#text': this.bibl[i].text,
            ref: {
              target: this.bibl[i].url,
              '#text': this.bibl[i].url,
            },
          });
        }

        // Prepare correspDescData
        for (let i = 0; i < this.correspDesc.length; i += 1) {
          const persNames = {
            sender: [],
            receiver: [],
          };
          const orgNames = {
            sender: [],
            receiver: [],
          };
          const placeNames = {
            sender: [],
            receiver: [],
          };

          ['sender', 'receiver'].forEach((t) => {
            for (let j = 0; j < this.correspDesc[i][t].persName.length; j += 1) {
              if (!this.correspDesc[i][t].persName[j].ref
                  && !this.correspDesc[i][t].persName[j].conjecture
                  && !this.correspDesc[i][t].persName[j].uncertain) {
                if (this.correspDesc[i][t].persName[j].organisation) {
                  orgNames[t].push(this.correspDesc[i][t].persName[j].text);
                } else {
                  persNames[t].push(this.correspDesc[i][t].persName[j].text);
                }
              } else if (this.correspDesc[i][t].persName[j].organisation) {
                orgNames[t].push({
                  '#text': this.correspDesc[i][t].persName[j].text,
                  ref: this.correspDesc[i][t].persName[j].ref,
                  evidence: (this.correspDesc[i][t].persName[j].conjecture) ? 'conjecture' : null,
                  cert: (this.correspDesc[i][t].persName[j].uncertain) ? 'low' : null,
                });
              } else {
                persNames[t].push({
                  '#text': this.correspDesc[i][t].persName[j].text,
                  ref: this.correspDesc[i][t].persName[j].ref,
                  evidence: (this.correspDesc[i][t].persName[j].conjecture) ? 'conjecture' : null,
                  cert: (this.correspDesc[i][t].persName[j].uncertain) ? 'low' : null,
                });
              }
            }
            persNames[t] = (persNames[t].length === 1) ? persNames[t][0] : persNames[t];
            orgNames[t] = (orgNames[t].length === 1) ? orgNames[t][0] : orgNames[t];
          });

          ['sender', 'receiver'].forEach((t) => {
            if (!this.correspDesc[i][t].placeName.ref
                && !this.correspDesc[i][t].placeName.conjecture
                && !this.correspDesc[i][t].placeName.uncertain) {
              placeNames[t] = this.correspDesc[i][t].placeName.text;
            } else {
              placeNames[t] = {
                '#text': this.correspDesc[i][t].placeName.text,
                ref: this.correspDesc[i][t].placeName.ref,
                evidence: (this.correspDesc[i][t].placeName.conjecture) ? 'conjecture' : null,
                cert: (this.correspDesc[i][t].placeName.uncertain) ? 'low' : null,
              };
            }
          });

          json.teiHeader.profileDesc.correspDesc.push({
            source: `#${this.getUuid(this.correspDesc[i].bibl)}`, // CHECK uuid -> Has to be changed
            ref: this.correspDesc[i].ref,
            key: this.correspDesc[i].key,
            correspAction: [
              {
                type: 'sent',
                persName: persNames.sender,
                orgName: orgNames.sender,
                placeName: placeNames.sender,
                date: {
                  when: (this.correspDesc[i].sender.date === 'when') ? this.correspDesc[i].sender.when : null,
                  from: (this.correspDesc[i].sender.date === 'span') ? this.correspDesc[i].sender.spanFrom : null,
                  to: (this.correspDesc[i].sender.date === 'span') ? this.correspDesc[i].sender.spanTo : null,
                  notBefore: (this.correspDesc[i].sender.date === 'nba') ? this.correspDesc[i].sender.notBefore : null,
                  notAfter: (this.correspDesc[i].sender.date === 'nba') ? this.correspDesc[i].sender.notAfter : null,
                  '#text': this.correspDesc[i].sender.dateAsText,
                  cert: (this.correspDesc[i].sender.dateCert.uncertain) ? 'low' : null,
                  evidence: (this.correspDesc[i].sender.dateCert.conjecture) ? 'conjecture' : null,
                },
              },
              {
                type: 'received',
                persName: persNames.receiver,
                orgName: orgNames.receiver,
                placeName: placeNames.receiver,
                date: {
                  when: (this.correspDesc[i].receiver.date === 'when') ? this.correspDesc[i].receiver.when : null,
                  from: (this.correspDesc[i].receiver.date === 'span') ? this.correspDesc[i].receiver.spanFrom : null,
                  to: (this.correspDesc[i].receiver.date === 'span') ? this.correspDesc[i].receiver.spanTo : null,
                  notBefore: (this.correspDesc[i].receiver.date === 'nba') ? this.correspDesc[i].receiver.notBefore : null,
                  notAfter: (this.correspDesc[i].receiver.date === 'nba') ? this.correspDesc[i].receiver.notAfter : null,
                  '#text': this.correspDesc[i].receiver.dateAsText,
                  cert: (this.correspDesc[i].receiver.dateCert.uncertain) ? 'low' : null,
                  evidence: (this.correspDesc[i].receiver.dateCert.conjecture) ? 'conjecture' : null,
                },
              },
            ],
          });
        }

        json = this.clear(json);

        const xhr = new XMLHttpRequest();
        let xml = '';
        xhr.open('POST', `https://correspSearch.net/api/v1.1/converter/json2xml.xql?json-data=${JSON.stringify(json)}`);
        xhr.onload = (data) => {
          if (xhr.status === 200) {
            const xmls = new XMLSerializer();
            xml = xmls.serializeToString(data);

            this.downloadLink = `data:text/xml;charset=utf-8,${encodeURIComponent(xml)}`;
          } else {
            console.info(xhr.status);
          }
        };
        xhr.send();

        this.downloadFileName = {
          json: `${this.meta.title.replace(/ /g, '_')}-${now}.json`,
          xml: `${this.meta.title.replace(/ /g, '_')}-${now}.xml`,
        };

        if (!xml) this.downloadLink = `data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(json))}`;
      }
    },

    // Clear empty properties
    clear(o) {
      const obj = o;
      Object.entries(obj).forEach((e) => {
        if (obj[e[0]] && typeof obj[e[0]] === 'object') {
          this.clear(obj[e[0]]);
          if (Object.keys(obj[e[0]]).length === 0) {
            delete obj[e[0]];
          }
        } else if (obj[e[0]] === '' || obj[e[0]] === null) {
          delete obj[e[0]];
        }
      });
      return obj;
    },

    // Get UUID by ID
    getUuid(id) {
      for (let u = 0; u < this.bibl.length; u += 1) {
        if (this.bibl[u].id === id) {
          return this.bibl[u].uuid;
        }
      }
      return 0;
    },
  },
  mounted() {
    this.prepare();
    console.log(this.correspDescFails);
  },
  computed: {
    // Get invalid Entries
    getInvalidCorrespDesc() {
      const results = [];
      for (let i = 0; i < this.correspDescFails.length; i += 1) {
        results.push({
          key: (this.correspDesc[this.correspDescFails[i]].key) ? this.correspDesc[this.correspDescFails[i]].key : null,
          id: this.correspDesc[this.correspDescFails[i]].id,
          from: this.correspDesc[this.correspDescFails[i]].sender.persName[0].text,
          to: this.correspDesc[this.correspDescFails[i]].receiver.persName[0].text,
        });
      }
      return results;
    },
  },
};
</script>



