<template>
  <div id="app">

    <b-nav tabs class="ml-0">
      <b-nav-item v-on:click="nav='start'"
                  v-bind:active="(nav==='start')">{{ labels.start }}</b-nav-item>
      <b-nav-item v-on:click="nav='meta'"
                  v-bind:active="(nav==='meta')">{{ labels.step1 }}</b-nav-item>
      <b-nav-item v-on:click="nav='bibl'"
                  v-bind:active="(nav==='bibl')">{{ labels.step2 }}</b-nav-item>
      <b-nav-item v-on:click="nav='correspDesc'"
                  v-bind:active="(nav==='correspDesc')">{{ labels.step3 }}</b-nav-item>
      <b-nav-item v-on:click="nav='save'"
                  v-bind:active="(nav==='save')">{{ labels.step4 }}</b-nav-item>
    </b-nav>

    <div class="container pt-5 pb-5 bg-white">
      <start v-bind:labels="labels"
             v-bind:pArrays="pArrays"
             v-if="nav === 'start'"></start>

      <metaData v-bind:labels="labels"
                v-bind:metaData="metaData"
                v-if="nav === 'meta'"></metaData>

      <biblData v-bind:labels="labels"
                v-bind:biblData="biblData"
                v-if="nav === 'bibl'"></biblData>

      <correspDescData v-bind:labels="labels"
                v-bind:correspDescData="correspDescData"
                v-bind:pArrays="pArrays"
                v-if="nav === 'correspDesc'"></correspDescData>

      <save v-bind:labels="labels"
            v-bind:metaData="metaData"
            v-bind:biblData="biblData"
            v-bind:correspDescData="correspDescData"
            v-if="nav === 'save'"></save>
    </div>
  </div>
</template>

<script>
/*
 * CMIF-Creator
 * v1.1
 * May 2018
 *
 * http://www.correspsearch.net
 */
import start from './components/start';
import metaData from './components/meta';
import biblData from './components/bibl';
import correspDescData from './components/correspDesc';
import save from './components/save';

export default {
  name: 'app',
  data() {
    return {
      labels: {
        start: '',
      },
      nav: 'start',
      metaData: {
        title: '',
        editor: {
          name: '',
          email: '',
        },
        publisher: {
          name: '',
          url: '',
        },
        url: '',
        licence: '',
      },
      biblData: [
        {
          id: 0,
          uuid: '',
          type: null,
          url: '',
          text: '',
          selected: '',
        },
      ],
      pArrays: {
        persons: [],
        places: [],
      },
      correspDescData: [
        {
          id: 0,
          visible: true,

          header: '',

          ref: '',
          refHidden: true,

          key: '',

          bibl: 0,

          sender: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
          receiver: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
        },
      ],
    };
  },
  components: {
    start,
    metaData,
    biblData,
    correspDescData,
    save,
  },
  methods: {
    navigate(target) {
      this.nav = target;
    },
    check(target, string) {
      let rp = new RegExp();
      switch (target) {
        case 'url': {
          rp = new RegExp(/^http(s)?\:\/\/[-a-zA-Z0-9@:%_\+.~#?&\/\/=]{2,256}\.[a-z]{2,4}\b(\/[-a-zA-Z0-9@:%_\+.~#?&\/\/=]*)?/gi);
          return rp.test(string);
        }
        case 'email': {
          rp = new RegExp(/^[a-zA-Z0-9.!#$%&*+\/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/);
          return rp.test(string);
        }
        case 'person': {
          rp = new RegExp(/^(http(s)?\:\/\/d\-nb\.info\/gnd\/?)|^(http(s)?\:\/\/viaf\.org\/viaf\/?)|^(http(s)?\:\/\/catalogue\.bnf\.fr\/?)|^(http(s)?\:\/\/lccn\.loc\.gov\/?)/g);
          return rp.test(string);
        }
        case 'place': {
          rp = new RegExp(/^http\:\/\/www\.geonames\.org\/?/g);
          return rp.test(string);
        }
        case 'date': {
          rp = new RegExp(/^(([0-1][0-9][0-9][0-9])$)|^(([0-1][0-9][0-9][0-9])\-([0]{0,1})([1-9]|[1][0-2])$)|^(([0-1][0-9][0-9][0-9])\-([0]{0,1})([1-9]|[1][0-2])\-([0-9][1-9]|[1-2][0-9]|[3][0-1])$)/g);
          return rp.test(string);
        }
        default:
          return false;
      }
    },
  },
  beforeCreate() {
    const xhr = new XMLHttpRequest();
    xhr.open('GET', '/static/labels.xml');
    xhr.onload = () => {
      if (xhr.status === 200) {
        const parser = new DOMParser();
        const xml = parser.parseFromString(xhr.responseText, 'application/xml');
        const url = new URL(window.location.href);
        const paramLang = url.searchParams.get('l');
        const lang = (paramLang !== 'en' && paramLang !== 'de') ? 'de' : paramLang;
        for (let i = 0; i < xml.children[0].children.length; i += 1) {
          if (xml.children[0].children[i].attributes[0].value === String(lang)) {
            for (let j = 0; j < xml.children[0].children[i].children.length; j += 1) {
              this.labels[xml.children[0].children[i].children[j].attributes[0].value]
              = xml.children[0].children[i].children[j].innerHTML;
            }
          }
        }
      } else {
        console.info(xhr.status);
      }
    };
    xhr.send();
  },
};
</script>

<style>

.header {
  background-color: #2A7299;
  color: #fff;
}

body {
  background-color: #606060 !important;
  color: #fff;
  font-family: 'PT Serif' !important;
}

h1 {
  font-size: 2.0em !important;
  font-weight: bold !important;
  font-family: 'PT Serif' !important;
}
h2 {
  font-size: 1.5em !important;
  font-weight: bold !important;
}
h3 {
  font-size: 1.2em !important;
  font-weight: bold !important;
  margin-bottom: 0.5em !important;
}
h4 {
  font-size: 1.2em !important;
  margin-bottom: 0.5em !important;
}

p {
  margin-bottom: 0.1em;
  line-height: 1.4;
}

.navbar {
  padding: 0 !important;
}

.navbar-brand {
  font-size: 1.0em;
  font-weight: bold;
}

.navbar-nav {
  margin: auto;
}

.navbar .nav-link {
  color: #fff !important;
  padding: 0.8em;
  padding-right: 1rem !important;
  padding-left: 1rem !important;
}

.navbar .nav-item {
  padding-top: 4px;
}

.navbar .form-inline {
  margin-bottom: 0;
}

.navbar .nav-item:hover, .navbar .nav-item.active {
  background-color: #545454;
}

.cs-contentBox {
  background-color: #fff;
  padding: 1.5em 0 1em 0;
}

.cs-navbar {
  background-color: #383838;
  color: #fff;
}

.cs-contentTop {
  position: relative;
  padding: 2em 0 1em 0;
  min-height: 175px;
  font-family: PTSans;
  background-color: #2A7299;
  color: white;
}

.cs-contentBottom {
	position: relative;
	min-height: 45%;
	padding: 1.5em 0 1em 0;
	background-color: #EAEAEA;
}

#app {
  font-family: 'PT Serif' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

#app .nav-link {
  background-color: #e0e0e0;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border: 0;
  color: #666;
}

#app .nav-link a {
  color: #666;
}

#app .nav-link.active {
  border: 0px !important;
  background-color: #fff;
}

.p-px-11 {
  padding-left: 11px !important;
}

.labelOnTop > label {
  position: static !important;
  padding-top:2px;
}

.highlighted {
  background-color: #f5f5f5 !important;
}

.page-item {
  margin-top: 0px !important;
}

</style>



