<template>
  <div class="start">
    <b-container>
      <b-row>
        <b-col>
          <h3>{{ label.head1 }}</h3>
          {{ label.startDesc }}
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col>
          <h4>{{ label.startNew }}</h4>
          <b-button v-on:click="startNew" 
                    v-if="!this.$parent.metaData.title" 
                    class="btn-block">{{ label.start }}</b-button>
          <b-button v-b-modal.modalStartNew 
                    v-if="this.$parent.metaData.title" 
                    class="btn-block">{{ label.start }}</b-button>
          <b-modal id="modalStartNew"
             v-if="this.$parent.metaData.title"
             ref="modal"
             v-bind:title="label.alertrestarthead"
             v-on:ok="startNew">
             <b-alert show variant="danger">{{ label.alertrestart }}</b-alert>
          </b-modal>
        </b-col>
        <b-col>
          <h4>{{ label.loadJson }}</h4>
          <b-form-file accept=".xml, .json" 
                       v-model="file" 
                       v-on:input="load"></b-form-file>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {
  name: 'start',
  props: {
    labels: {
      type: Object,
      default: () => ({}),
    },
    pArrays: {
      type: Object,
      twoWays: true,
      default: () => ({
        persons: [],
        places: [],
      }),
    },
  },
  data() {
    return {
      file: '',
      label: this.labels,
      persons: this.pArrays.persons,
      places: this.pArrays.places,
      beginAnew: false,
    };
  },
  methods: {
    // Start new CMIF File
    startNew() {
      this.$parent.nav = 'meta';
      this.$parent.metaData = {
        title: '',
        editor: {
          name: '',
          email: '',
        },
        publisher: {
          name: '',
          url: '',
        },
        url: '',
        licence: '',
      };
      this.$parent.biblData = [
        {
          id: 0,
          uuid: '',
          type: null,
          url: '',
          text: '',
          selected: '',
        },
      ];
      this.$parent.correspDescData = [
        {
          id: 0,
          visible: true,

          header: '',

          ref: '',
          refHidden: true,

          key: '',

          bibl: 0,

          sender: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
          receiver: {
            persName: [
              {
                text: '',
                ref: '',

                conjecture: false,
                uncertain: false,

                organisation: false,

                // Autocomplete
                open: false,
                highlighted: 0,
                selected: false,
              },
            ],
            placeName: {
              text: '',
              ref: '',

              conjecture: false,
              uncertain: false,

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: false,
                suggestions: [],
                all: 0,
              },
            },

            date: '',
            dateCert: {
              conjecture: false,
              uncertain: false,
            },
            when: '',
            notBefore: '',
            notAfter: '',
            spanFrom: '',
            spanTo: '',
            dateAsText: '',
            dateAsTextHidden: true,
          },
        },
      ];
    },

    // Load existing JSON or XML
    load() {
      const reader = new FileReader();
      reader.onload = () => {
        const fileExtension = this.file.name.split('.').pop();
        if (fileExtension === 'xml') {
          const xhr = new XMLHttpRequest();
          xhr.open('POST', `https://correspSearch.net/api/v1.1/converter/xml2json.xql?xml-data=${reader.result}`);
          xhr.onload = () => {
            if (xhr.status === 200) {
              const json = JSON.parse(xhr.responseText);
              this.loadJsonToFileDesc(json.teiHeader.fileDesc);
              this.loadJsonToBibl(json.teiHeader.fileDesc.sourceDesc.bibl);
              this.loadJsonToCorrespDesc(json.teiHeader.profileDesc.correspDesc);

              this.$parent.navigate('meta');
            } else {
              console.info(xhr.status);
            }
          };
          xhr.send();
        } else {
          const json = JSON.parse(reader.result);
          this.loadJsonToFileDesc(json.teiHeader.fileDesc);
          this.loadJsonToBibl(json.teiHeader.fileDesc.sourceDesc.bibl);
          this.loadJsonToCorrespDesc(json.teiHeader.profileDesc.correspDesc);

          this.$parent.navigate('meta');
        }
      };
      reader.readAsText(this.file);
    },

    // Load JSON-Data to Step1
    loadJsonToFileDesc(json) {
      this.$parent.metaData.title = (json.titleStmt.title) ? json.titleStmt.title : '';

      this.$parent.metaData.editor.name = (json.titleStmt.editor)
        ? json.titleStmt.editor['#text'] : '';
      this.$parent.metaData.editor.email = (json.titleStmt.editor)
        ? json.titleStmt.editor.email : '';

      if (json.publicationStmt.publisher
        && json.publicationStmt.publisher.ref
        && json.publicationStmt.publisher.ref['#text']) {
        this.$parent.metaData.publisher.name = (json.publicationStmt.publisher
          && json.publicationStmt.publisher.ref)
          ? json.publicationStmt.publisher.ref['#text'] : '';
        this.$parent.metaData.publisher.url = (json.publicationStmt.publisher
          && json.publicationStmt.publisher.ref)
          ? json.publicationStmt.publisher.ref.target : '';
      } else {
        this.$parent.metaData.publisher.name = (json.publicationStmt.publisher)
          ? json.publicationStmt.publisher.ref : '';
        this.$parent.metaData.publisher.url = '';
      }

      this.$parent.metaData.idno = (json.publicationStmt.idno)
        ? json.publicationStmt.idno['#text'] : '';
      this.$parent.metaData.date = (json.publicationStmt.date)
        ? json.publicationStmt.date.when : '';
      this.$parent.metaData.licence = (json.publicationStmt.availability)
        ? json.publicationStmt.availability.licence['#text'] : '';
    },

    // Load JSON-Data to Step2
    loadJsonToBibl(json) {
      this.$parent.biblData = [];
      for (let i = 0; i < json.length; i += 1) {
        this.$parent.biblData.push({
          id: i,
          uuid: (json[i]['xml:id'] === undefined) ? '' : json[i]['xml:id'],
          type: (json[i].type === undefined) ? '' : json[i].type,
          url: (json[i].ref === undefined) ? '' : json[i].ref.target,
          text: (json[i]['#text'] === undefined) ? '' : json[i]['#text'],
          selected: false,
        });
      }
    },

    // Load JSON-Data to Step3
    loadJsonToCorrespDesc(json) {
      this.$parent.correspDescData = [];
      for (let i = 0; i < json.length; i += 1) {
        const dateForm = {};
        ['sender', 'receiver'].forEach((e) => {
          const t = (e === 'sender') ? 0 : 1;
          if (json[i]
            && json[i].correspAction
            && json[i].correspAction[t]
            && json[i].correspAction[t].date) {
            if (json[i].correspAction[t].date.when) {
              dateForm[e] = 'when';
            } else if (json[i].correspAction[t].date.notBefore) {
              dateForm[e] = 'nba';
            } else if (json[i].correspAction[t].date.from) {
              dateForm[e] = 'span';
            } else {
              dateForm[e] = 'na';
            }
          } else {
            dateForm[e] = 'na';
          }
        });

        const placeName = {};
        ['sender', 'receiver'].forEach((e) => {
          const t = (e === 'sender') ? 0 : 1;
          if (json[i]
            && json[i].correspAction
            && json[i].correspAction[t]
            && json[i].correspAction[t].placeName) {
            placeName[e] = (json[i].correspAction[t].placeName['#text'])
              ? json[i].correspAction[t].placeName['#text'] : json[i].correspAction[t].placeName;
          }
        });

        const persNames = { sender: [], receiver: [] };
        ['sender', 'receiver'].forEach((e) => {
          const t = (e === 'sender') ? 0 : 1;
          ['orgName', 'persName'].forEach((a) => {
            if (json[i]
              && json[i].correspAction
              && json[i].correspAction[t]
              && json[i].correspAction[t][a]) {
              if (typeof json[i].correspAction[t][a] === 'object') {
                if (json[i].correspAction[t][a]['#text']) {
                  persNames[e].push({
                    text: json[i].correspAction[t][a]['#text'],
                    ref: json[i].correspAction[t][a].ref,

                    conjecture: (json[i].correspAction[t][a].evidence === 'conjecture'),
                    uncertain: (json[i].correspAction[t][a].cert === 'low'),

                    organisation: (a === 'orgName'),

                    // Autocomplete
                    open: false,
                    highlighted: 0,
                    selected: false,
                  });
                } else {
                  json[i].correspAction[t][a].forEach((p) => {
                    persNames[e].push({
                      text: (p['#text']) ? p['#text'] : p,
                      ref: (json[i].correspAction[t][a][p]) ? json[i].correspAction[t][a][p].ref : '',

                      conjecture: (json[i].correspAction[t][a][p]
                        && json[i].correspAction[t][a][p].evidence === 'conjecture'),
                      uncertain: (json[i].correspAction[t][a][p]
                        && json[i].correspAction[t][a][p].cert === 'low'),

                      organisation: (a === 'orgName'),

                      // Autocomplete
                      open: false,
                      highlighted: 0,
                      selected: false,
                    });
                  });
                }
              } else if (typeof json[i].correspAction[t][a] === 'string') {
                persNames[e].push({
                  text: json[i].correspAction[t][a],
                  ref: '',

                  conjecture: false,
                  uncertain: false,

                  organisation: (a === 'orgName'),

                  // Autocomplete
                  open: false,
                  highlighted: 0,
                  selected: false,
                });
              }
            }
          });
        });

        ['sender', 'receiver'].forEach((e) => {
          for (let k = 0; k < persNames[e].length; k += 1) {
            let match = false;
            if (this.persons.length === 0) {
              this.persons.push(persNames[e][k]);
            } else {
              for (let l = 0; l < this.persons.length; l += 1) {
                match = (persNames[e][k].text === this.persons[l].text);
                if (match) break;
              }
              if (!match) {
                this.persons.push(persNames[e][k]);
              }
            }
          }
        });

        ['sender', 'receiver'].forEach((e) => {
          const t = (e === 'sender') ? 0 : 1;
          let match = false;
          if (placeName[e]) {
            if (this.places.length === 0) {
              this.places.push({
                text: placeName[e],
                ref: (json[i]
                  && json[i].correspAction
                  && json[i].correspAction[t]
                  && json[i].correspAction[t].placeName)
                  ? json[i].correspAction[t].placeName.ref : '',
              });
            } else {
              for (let l = 0; l < this.places.length; l += 1) {
                match = (placeName[e] === this.places[l].text);
                if (match) break;
              }
              if (!match) {
                this.places.push({
                  text: placeName[e],
                  ref: (json[i]
                    && json[i].correspAction
                    && json[i].correspAction[t]
                    && json[i].correspAction[t].placeName)
                    ? json[i].correspAction[t].placeName.ref : '',
                });
              }
            }
          }
        });

        let head = '';
        head = (json[i].key) ? `<span class="badge badge-info mr-1">${json[i].key}</span>` : '';
        head += (persNames.sender[0].text) ? `<span class="badge badge-secondary mr-1">${this.label.headBy}</span><small class="mr-2 ml-1">${persNames.sender[0].text}</small>` : '';
        head += (persNames.receiver[0].text) ? `<span class="badge badge-secondary mr-1">${this.label.headTo}</span><small class="mr-2 ml-1">${persNames.receiver[0].text}</small>` : '';

        this.$parent.correspDescData.push({
          id: i,
          visible: true,

          header: head,

          ref: (json[i] && json[i].ref) ? json[i].ref : '',
          refHidden: !(json[i] && json[i].ref),

          key: (json[i]) ? json[i].key : '',

          bibl: this.getRealBibId(json[i].source.replace('#', '')),

          sender: {
            persName: persNames.sender, // Muss angepasst werden
            placeName: {
              text: placeName.sender,
              ref: (json[i]
                && json[i].correspAction
                && json[i].correspAction[0]
                && json[i].correspAction[0].placeName
                && json[i].correspAction[0].placeName.ref)
                ? json[i].correspAction[0].placeName.ref : '',

              conjecture: (json[i]
                && json[i].correspAction
                && json[i].correspAction[0]
                && json[i].correspAction[0].placeName
                && json[i].correspAction[0].placeName.evidence === 'conjecture'),
              uncertain: (json[i]
                && json[i].correspAction
                && json[i].correspAction[0]
                && json[i].correspAction[0].placeName
                && json[i].correspAction[0].placeName.cert === 'low'),

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: 0,
                suggestions: [],
                all: 0,
              },
            },

            date: dateForm.sender,
            dateCert: {
              conjecture: (json[i]
                && json[i].correspAction
                && json[i].correspAction[0]
                && json[i].correspAction[0].date
                && json[i].correspAction[0].date.evidence === 'conjecture'),
              uncertain: (json[i]
                && json[i].correspAction
                && json[i].correspAction[0]
                && json[i].correspAction[0].date
                && json[i].correspAction[0].date.cert === 'low'),
            },
            when: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date.when)
              ? json[i].correspAction[0].date.when : '',
            notBefore: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date.notBefore)
              ? json[i].correspAction[0].date.notBefore : '',
            notAfter: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date.notAfter)
              ? json[i].correspAction[0].date.notAfter : '',
            spanFrom: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date.from)
              ? json[i].correspAction[0].date.from : '',
            spanTo: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date.to)
              ? json[i].correspAction[0].date.to : '',
            dateAsText: (json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date)
              ? json[i].correspAction[0].date['#text'] : '',
            dateAsTextHidden: !(json[i].correspAction
              && json[i].correspAction[0]
              && json[i].correspAction[0].date
              && json[i].correspAction[0].date['#text']),
          },
          receiver: {
            persName: persNames.receiver,
            placeName: {
              text: placeName.receiver,
              ref: (json[i]
                && json[i].correspAction
                && json[i].correspAction[1]
                && json[i].correspAction[1].placeName
                && json[i].correspAction[1].placeName.ref)
                ? json[i].correspAction[1].placeName.ref : '',

              conjecture: (json[i]
                && json[i].correspAction
                && json[i].correspAction[1]
                && json[i].correspAction[1].placeName
                && json[i].correspAction[1].placeName.evidence === 'conjecture'),
              uncertain: (json[i]
                && json[i].correspAction
                && json[i].correspAction[1]
                && json[i].correspAction[1].placeName
                && json[i].correspAction[1].placeName.cert === 'low'),

              // Autocomplete
              open: false,
              highlighted: 0,
              selected: false,

              // Geonames Suggestions
              geo: {
                open: false,
                highlighted: 0,
                suggestions: [],
                all: 0,
              },
            },

            date: dateForm.receiver,
            dateCert: {
              conjecture: (json[i]
                && json[i].correspAction
                && json[i].correspAction[1]
                && json[i].correspAction[1].date
                && json[i].correspAction[1].date.evidence === 'conjecture'),
              uncertain: (json[i]
                && json[i].correspAction
                && json[i].correspAction[1]
                && json[i].correspAction[1].date
                && json[i].correspAction[1].date.cert === 'low'),
            },
            when: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date.when)
              ? json[i].correspAction[1].date.when : '',
            notBefore: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date.notBefore)
              ? json[i].correspAction[1].date.notBefore : '',
            notAfter: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date.notAfter)
              ? json[i].correspAction[1].date.notAfter : '',
            spanFrom: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date.from)
              ? json[i].correspAction[1].date.from : '',
            spanTo: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date.to)
              ? json[i].correspAction[1].date.to : '',
            dateAsText: (json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date)
              ? json[i].correspAction[1].date['#text'] : '',
            dateAsTextHidden: !(json[i].correspAction
              && json[i].correspAction[1]
              && json[i].correspAction[1].date
              && json[i].correspAction[1].date['#text']),
          },
        });
      }
    },

    // Get id from uuid
    getRealBibId(uuid) {
      let result = '';
      for (let i = 0; i < this.$parent.biblData.length; i += 1) {
        result = (this.$parent.biblData[i].uuid === uuid) ? i : 0;
        break;
      }
      return result;
    },
  },
};
</script>


